.container-absolute-top {
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}
@media (min-width: 768px) {
  .row-md-eq {
    display: flex;
  }
}
.col-middle {
  flex-direction: column;
  vertical-align: middle;
  align-self: center;
}
.col-flex {
  display: flex;
  flex-direction: column;
}
.col-flex .align-bottom {
  margin-top: auto;
}
.navbar {
  border: 0;
}
.navbar-right {
  margin-right: 0;
}
.navbar-brand {
  padding-bottom: 10px;
  padding-top: 10px;
}
.navbar-brand > img {
  max-height: 60px;
}
.navbar-toggle .icon-bar {
  transition: all 0.2s ease-in-out;
}
.navbar-toggle .bar-top {
  transform-origin: 10% 10%;
  transform: rotate(45deg);
}
.navbar-toggle .bar-middle {
  opacity: 0;
}
.navbar-toggle .bar-bottom {
  transform-origin: 10% 90%;
  transform: rotate(-45deg);
}
.navbar-toggle.collapsed .bar-top,
.navbar-toggle.collapsed .bar-bottom {
  transform: rotate(0);
}
.navbar-toggle.collapsed .bar-middle {
  opacity: 1;
}
.img-responsive {
  width: 100%;
}
.section {
  padding-bottom: 60px;
  padding-top: 60px;
}
.section-alt {
  background-color: #ebf1f2;
}
.section-primary {
  background-color: #009eeb;
  color: #fff;
}
.section-primary a {
  color: #fff;
}
.section-primary a:hover,
.section-primary a:focus {
  color: #ccecfa;
}
.section-primary .divider {
  border-color: #0088ca;
}
.section-carousel.slick-dotted.slick-slider {
  margin-bottom: 0;
}
.section-carousel.slick-dotted.slick-slider .slick-arrow {
  z-index: 10;
}
.section-carousel.slick-dotted.slick-slider .slick-prev {
  left: 15px;
}
.section-carousel.slick-dotted.slick-slider .slick-next {
  right: 15px;
}
.section-carousel.slick-dotted.slick-slider .slick-dots {
  bottom: 15px;
}
.section-carousel.slick-dotted.slick-slider .slick-dots li button:before,
.section-carousel.slick-dotted.slick-slider .slick-dots li.slick-active button:before {
  color: #fff;
}
.section-intro {
  background: #5e6d7b url('../img/hero-bg.jpg') center center no-repeat;
  background-size: cover;
  color: #fff;
  margin-bottom: 0;
  padding-top: 140px;
}
.section-intro .section-header {
  font-size: 24px;
  margin-bottom: 60px;
}
@media (min-width: 768px) {
  .section-intro .section-header {
    font-size: 36px;
    margin-bottom: 0;
  }
}
.label {
  border-radius: 0;
  font-weight: 300;
  text-transform: uppercase;
}
.read-more {
  display: inline-block;
  margin-top: 10px;
  padding-bottom: 20px;
  padding-left: 40px;
  position: relative;
}
.read-more:before {
  border: 20px solid #f41215;
  border-right-color: transparent;
  border-top-color: transparent;
  bottom: 10px;
  content: "";
  display: block;
  left: 0;
  position: absolute;
}
.read-more:after {
  bottom: 14px;
  color: #fff;
  content: "\2192";
  font-weight: 200;
  left: 8px;
  line-height: 1;
  position: absolute;
}
.entry {
  margin-bottom: 60px;
}
@media (min-width: 768px) {
  .entry-md-compact {
    margin-bottom: 20px;
  }
}
.entry-title:first-child {
  margin-top: 0;
}
.entry-title > small {
  display: block;
  margin-bottom: 1em;
}
.panel {
  border-width: 8px;
  box-shadow: none;
}
.divider {
  border-top-width: 2px;
  margin-left: 0;
  margin-right: 0;
  max-width: 180px;
}
.divider-center {
  margin-left: auto;
  margin-right: auto;
}
.divider-danger {
  border-color: #f41215;
}
.list {
  list-style: none;
  padding-left: 0;
}
.list li {
  padding-left: 1.5em;
  position: relative;
}
.list li:before {
  content: "";
  display: inline-block;
  left: 0;
  position: absolute;
  top: 0;
}
.list-checkbox {
  color: #999;
  padding-left: 0;
}
.list-checkbox li {
  margin-bottom: 20px;
  padding-left: 2em;
}
.list-checkbox li:before {
  background-image: url('../img/sprite-checkbox.png');
  background-repeat: none;
  background-size: 1em auto;
  height: 1em;
  top: 2px;
  width: 1em;
}
.list-checkbox li.checked {
  color: #00a0e8;
}
.list-checkbox li.checked:before {
  background-position: left bottom;
}
.list-bulleted li:before {
  color: #f41215;
  content: "\25cf";
}
.list-relaxed li {
  margin-bottom: 10px;
}
.footer {
  background-color: #0088ca;
  color: #fff;
  text-align: center;
}
.footer a {
  color: #fff;
}
.footer a:hover,
.footer a:focus {
  color: #ccecfa;
}
.footer .section-title {
  font-size: 80px;
}
.footer .divider {
  border-color: #009eeb;
}
.footer .text-muted {
  color: #80d0f4;
}
.fb-page {
  display: block !important;
}
.follow-us {
  margin-bottom: 30px;
  margin-top: 30px;
  text-align: center;
}
.follow-us .list-inline {
  font-size: 75%;
  font-weight: 500;
  text-transform: uppercase;
}
.follow-us a {
  color: inherit;
}
.follow-us a:hover,
.follow-us a:focus {
  text-decoration: none;
}
.follow-us img {
  display: block;
  margin: 0 auto 5px;
  max-width: 28px;
}
/*# sourceMappingURL=style.css.map */